import React, { useState, useEffect } from "react";
import { useGlobalContext } from "../../context";
import classes from "./Polyplast.module.css";
import polyplast_img from "../../assets/slider_polyplast.png";
import Footer from "../../components/Footer/Footer";
import facts from "../../facts";

const Polyplast = () => {
  const { setPath } = useGlobalContext();
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setPath("business");
    const timeout = setTimeout(() => {
      setLoading(false);
    }, 4000);
    return () => {
      clearTimeout(timeout);
    };
    // eslint-disable-next-line
  }, []);

  return (
    <div className="Parent">
      {loading ? (
        <div className={`Container ${classes.Loading}`}>
          <h1>Dorun Fact!</h1>
          <h3>{facts[Math.floor(Math.random() * 5)]}</h3>
          <div className="loading"></div>
        </div>
      ) : (
        <React.Fragment>
          <div className={`Container ${classes.Polyplast}`}>
            <img
              src={polyplast_img}
              alt="4 dustbins with Polyplast written on it"
              className={classes.Image}
            />
            <div>
              <h1 className={classes.Title}>Dorun Polyplast</h1>
              <p className={classes.Text}>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsum
                corporis veritatis soluta dignissimos. Officiis molestias saepe
                consequuntur eius sint, labore error iusto non expedita
                praesentium, totam nostrum, odio provident repellat sit
                architecto amet minus. Cupiditate nam harum asperiores amet
                dolore. Ducimus officia porro quo quisquam cumque fuga, quos
                repellendus dolorem?
              </p>
            </div>
          </div>
          <Footer />
        </React.Fragment>
      )}
    </div>
  );
};

export default Polyplast;
